import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
import BaseRefer from './BaseRefer';
import DropDownRefer from './DropDownRefer';
import PopRefer from './PopRefer';
import ReferWithUnit from './ReferWithUnit';
import ArrayTree from './components/ArrayTree';
import './index.less';
class Refer extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		const { refType, type, unitProps } = this.props;
		if (type === 'dropDown' && refType === 'grid') {
			return <DropDownRefer {...this.props} />;
		} else if (unitProps) {
			return <ReferWithUnit {...this.props} />;
		} else {
			return <PopRefer {...this.props} />;
		}
	}
}

Refer.BaseRefer = BaseRefer;
Refer.DropDownRefer = DropDownRefer;
Refer.PopRefer = PopRefer;
Refer.ArrayTree = ArrayTree;
Refer.ReferWithUnit = ReferWithUnit;

export default Refer;
