import React, { Component } from 'react';
import PopRefer from './PopRefer.js';
import Refer from './index.js';

class ReferWithUnit extends PopRefer {
	constructor(props) {
		super(props);
		this.state = {
			...this.state,
			unit: props.defaultUnitValue || {}
		};
	}
	renderPopoverHeaderExtend = () => {
		const { unitProps, isShowUnit, unitCondition, defaultUnitValue } = this.props;
		let props = { ...unitProps };
		if (unitCondition) {
			props = { ...unitProps, queryCondition: unitCondition };
		}
		return (
			isShowUnit && (
				<div style={{ width: '200px', marginLeft: '20px' }}>
					<Refer
						{...props}
						value={this.state.unit}
						onChange={(v) => {
							// this.loadSelectedData();
							this.setState(
								{
									unit: v
								},
								() => {
									this.refresh(false);
								}
							);
						}}
						popWindowClassName={'refer-unit-pop-window'}
					/>
				</div>
			)
		);
	};
}

export default ReferWithUnit;
