import React, { Component } from 'react';
import {base} from 'nc-lightapp-front';
// import Loading from '../../../base/nc_Loading';
const {NCLoading:Loading} = base;

export default class ReferDropDown extends Component {
	constructor(props) {
		super(props);
		this.state = {};
	}
	render() {
		let {
			dropDownShow,
			referVal,
			isSelectedShow,
			columnConfig,
			dropDownList,
			getSelections,
			selectedValues,
			singleSelect,
			hiddenInput,
			maxWidth,
			loading,
			idKey,
			onlyLeafCanSelect,
			refType
		} = this.props;

		let style = {},
			offsetWidth = this.listTitle && this.listTitle.offsetWidth,
			width =
				maxWidth && maxWidth < 280
					? offsetWidth < maxWidth ? maxWidth : offsetWidth < 280 ? offsetWidth : 280
					: maxWidth;
		if (offsetWidth > width) {
			style = {
				overflow: 'hidden',
				textOverflow: 'ellipsis',
				width
			};
		}

		if (dropDownShow && !referVal) {
			columnConfig = [
				{
					name: [ '编码', '名称' ],
					code: [ 'refcode', 'refname' ]
				}
			];
		}

		return (
			<div
				className="refer-cascading-list"
				style={{
					width,
					display: dropDownShow || isSelectedShow ? 'flex' : 'none'
				}}
			>
				<Loading container={this} show={loading} />
				<div className="scroll-div">
					<p
						className="list-title"
						ref={(dom) => {
							this.listTitle = dom;
						}}
						style={style}
					>
						{dropDownShow ? referVal ? '搜索结果' : '历史记录' : isSelectedShow ? '已选择' : ''}
						<span>（{columnConfig[columnConfig.length - 1].name.join('/')}）</span>
					</p>
					<div className="refer-scroll">
						<ul className="refer-cascading-item" style={{ width: '100%' }}>
							{(dropDownShow
								? (dropDownList.rows || []).filter((e) => {
										if (refType === 'tree') {
											// 排除禁用的节点
											let disabled = !((onlyLeafCanSelect && e.isleaf) || !onlyLeafCanSelect);
											if (!disabled && e.values && e.values.ReturnOnTree) {
												disabled =
													e.values.ReturnOnTree.display === 'N' ||
													e.values.ReturnOnTree.value === 'N';
											}
											return !disabled;
										} else {
											return true;
										}
									})
								: getSelections()).map((item, i) => {
								let label = columnConfig[columnConfig.length - 1].code
									.map((e, i) => {
										return item[e] || (item.values && item.values[e] && item.values[e].value);
									})
									.join('/');
								return (
									<li
										title={label}
										className={
											'clearfix refer-li' +
											(selectedValues.has(item[idKey])
												? isSelectedShow ? ' is-selected' : ' selected'
												: '')
										}
										key={i}
										onClick={(e) => {
											e.stopPropagation();
											singleSelect(item);
										}}
									>
										{label}
										{isSelectedShow && <i className="refer-del-i iconfont icon-qingkong" />}
									</li>
								);
							})}
						</ul>
					</div>
				</div>
				{isSelectedShow &&
				!dropDownShow && (
					<p
						onClick={() => {
							hiddenInput.select();
							document.execCommand('Copy');
						}}
						className="refer-copy"
					>
						复制
					</p>
				)}
			</div>
		);
	}
}
