import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import {base} from 'nc-lightapp-front';
const {NCTree:Tree} = base;
const TreeNode = Tree.NCTreeNode;

export default class ReferTree extends Component {
	static defaultProps = {
		defaultExpandAll: false,
		checkStrictly: true,
		autoExpandParent: false,
		id: 'refpk',
		pid: 'pid'
	};
	constructor(props) {
		super(props);
		this.state = {
			group: {},
			expandedKeys: {}
		};
	}

	componentWillMount() {
		this.group(this.props);
	}

	componentWillReceiveProps(nextProps) {
		this.group(nextProps);
	}

	group = (props) => {
		let group = {},
			expandedKeys = {};
		const { pid, id } = props;
		props.data.forEach((e) => {
			group[e[pid]] ? group[e[pid]].push(e) : (group[e[pid]] = [ e ]);
		});
		// array => object
		props.expandedKeys.forEach((e) => {
			expandedKeys[e] = true;
		});
		this.setState({
			group,
			expandedKeys
		});
	};

	makeDOM = (data) => {
		let { group, expandedKeys } = this.state;
		const { onlyLeafCanSelect, id, isTreeSearch } = this.props;
		if (typeof data === 'boolean' || data === undefined) {
			if (data === true || data === undefined || isTreeSearch) {
				return '';
			} else if (data === false) {
				return [];
			}
		}
		return data.map((e, i) => {
			let { _display, ...otherProps } = e,
				refpk = e[id],
				disabled = !((onlyLeafCanSelect && e.isleaf) || !onlyLeafCanSelect);
			if (!disabled && e.values && e.values.ReturnOnTree) {
				disabled = e.values.ReturnOnTree.display === 'N' || e.values.ReturnOnTree.value === 'N';
			}
			return (
				<TreeNode
					key={e._path || refpk}
					title={
						<span>
							{group[refpk] || e.isleaf === false ? (
								<span
									className={`refer-tree-switch iconfont ${expandedKeys[refpk]
										? 'icon-wenjianjiadakai'
										: 'icon-wenjianjia'}`}
								/>
							) : (
								<span className="refer-tree-point" />
							)}
							<span className={`refer-tree-title`}>{_display || e.refname}</span>
						</span>
					}
					treeNodeData={e}
					disableCheckbox={disabled}
				>
					{this.makeDOM(group[refpk] || e.isleaf)}
				</TreeNode>
			);
		});
	};

	render() {
		const { data, isTreelazyLoad, root, ...otherProps } = this.props;
		let { group } = this.state;
		return (
			<Tree
				showLine
				{...otherProps}
				// filterTreeNode={(node) => {
				// 	return node.props.treeNodeData.refname.includes('宣传品');
				// }}
			>
				{this.makeDOM(group['undefined'])}
			</Tree>
		);
	}
}
