import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
import {base,ajax,toast,viewModel} from 'nc-lightapp-front';
const ViewModel = viewModel;
const {
	NCLoading:Loading,
	NCCollapse:Collapse,
	NCTable:Table,
	NCRow:Row,
	NCCheckbox:Checkbox,
	NCButton:Button,
	NCPopover:Popover,
	NCPagination:Pagination,
	NCDnd:Dnd,
	NCTabs:Tabs,
	NCMenu:Menu,
	NCFormControl:FormControl,
	NCIcon:Icon,
	NCCol:Col,
	NCDropdown:Dropdown,


} = base;
// base
// import Loading from '../../base/nc_Loading';
// import Collapse from '../../base/nc_Collapse';
// import Icon from '../../base/nc_Icon';
// import FormControl from '../../base/nc_FormControl';
// import Row from '../../base/nc_Row';
// import Col from '../../base/nc_Col';
// import Dropdown from '../../base/nc_Dropdown';
// api
// import toast from '../../api/toast';
import { compare, compareArr } from './utils';
// import ajax from '../../api/ajax';
import DropDownOfRefer from './components/DropDownOfRefer';
// import ViewModel from '../../shell/viewmodel/viewmodel';
const MAX_CHOICE = 1000; // 最多选择条数
class BaseRefer extends Component {
	constructor(props) {
		super(props);
		this.state = {
			referVal: '', //输入框的值
			tableData: [
				{
					rows: [],
					page: {
						pageIndex: 0,
						pageSize: props.pageSize,
						totalPage: 1
					}
				}
			], //参照的数据
			// treeData: [ props.rootNode ], // 左树的值
			treeData: [ props.rootNode ], // 左树的值
			currentLevel: 0, //当前是第几级
			dropDownList: {
				page: {
					pageIndex: 0,
					pageSize: props.pageSize,
					totalPage: 1
				},
				rows: []
			}, //下拉搜索和历史记录
			dropDownShow: false, //搜索和历史记录下拉是否显示
			isShow: false, //参照下拉和弹窗是否显示
			loading: false, //loading是否显示
			selectedValues: new Map(), //已选的数据
			isSelectedShow: false,
			groupTreeData: {}
		};
		this.interval = 0;
		this.detailcategory = '';
		this.oldValue = null;
		this._value = new Map(); // 实际已选值
		if (!localStorage.referFlag) {
			localStorage.referHistory = '';
			localStorage.referFlag = 1;
		}
	}

	componentWillMount() {
		this.propsToState(this.props);
	}

	componentDidMount() {
		// 点击页面其他部分收起下拉
		document.addEventListener('click', this.close, true);
	}

	componentWillReceiveProps(nextProps) {
		this.propsToState(nextProps, this.props);
	}

	componentWillUnmount() {
		// 移除document事件
		document.removeEventListener('click', this.close, true);
	}
	// 刷新
	refresh = (clearCache = true) => {
		let { pageSize, isTreelazyLoad, rootNode, refType, appendToTreeNode, idKey } = this.props,
			{ activeKey } = this.state;
		// state数据清空
		let defaultState;
		defaultState = {
			tableData: [
				{
					rows: [],
					page: {
						pageIndex: 0,
						pageSize: pageSize,
						totalPage: 1
					}
				}
			], //参照的数据
			// treeData: [ rootNode ] // 左树的值
			treeData: [ rootNode ] // 左树的值
		};
		// 缓存数据清空
		this.clearCache();
		// 树表时清除左树选中
		if (refType === 'gridTree') {
			this.state.selectedKeys = [];
		}
		// 清空搜索框
		let newState = {
			...defaultState,
			selectedKeys: this.state.selectedKeys,
			searchVal: '',
			treeSearchVal: '',
			isTreeSearch: false,
			isSearch: false,
			expandedKeys: [ rootNode[idKey] ]
		};
		clearCache && (newState.selectedValues = new Map(JSON.parse(JSON.stringify(this._value)))); // 已选恢复到点开时
		this.setState(newState, () => {
			if (activeKey === '1') {
				// 查常用数据
				this.loadAndSetTableData({
					pageInfo: {
						pageIndex: -1,
						pageSize
					}
				});
			} else {
				// 查全部数据
				this.show();
			}
		});
	};
	checkValue = (searchPks) => {
		return new Promise((resolve) => {
			const { refType, isMultiSelectedEnabled } = this.props;
			let param = this.__getParam({
				searchPks
			});
			if (refType === 'tree') {
				this.loadTreeData(param).then((data) => {
					resolve(isMultiSelectedEnabled ? data.rows : data.rows[0]);
				});
			} else {
				this.loadTableData(param).then((data) => {
					resolve(isMultiSelectedEnabled ? data.rows : data.rows[0]);
				});
			}
		});
	};
	propsToState = async (props, prevProps) => {
		if (props.clickContainer) {
			let oldClick = props.clickContainer.props.onClick;
			this.clickContainer = {
				...props.clickContainer,
				props: {
					...props.clickContainer.props,
					onClick: () => {
						typeof oldClick === 'function' && oldClick();
						this.show();
					},
					className: props.clickContainer + ' refer-click-container'
				}
			};
			Object.defineProperty(this.clickContainer, 'props', {
				writable: false,
				configurable: false,
				value: this.clickContainer.props,
				enumerable: true
			});
		}
		// 同步props.value到state.selectedValues，保证外部输入的value和内部统一
		// 同步referVal
		// props.value 外部传进来的值
		// this._value 实际选中的值（上次选中的值上次选中的值）
		// state.selectedValue 当前选中的值（还没点确定）
		let { referVal, treeData, expandedKeys, isShow } = this.state,
			{ value: _value, foolValue, refType, alwaysCheckValue, idKey, rootNode } = props,
			value = foolValue ? this.foolValueToValue(foolValue) : _value;

		if (prevProps) {
			// idKey改变
			let rootId = (props.rootNode && props.rootNode[idKey]) || '',
				prevRootId = (prevProps.rootNode && prevProps.rootNode[prevProps.idKey]) || '';
			if (idKey !== prevProps.idKey) {
				treeData = [ props.rootNode ];
				expandedKeys = [ rootId ];
			}
		}

		if (props.isMultiSelectedEnabled) {
			// 多选
			let o = new Map();
			Array.isArray(value) || (value = []);
			if (!compareArr([ ...(this._value || new Map()).keys() ], value.map((e) => e[idKey]))) {
				if (alwaysCheckValue) {
					value = await this.checkValue(value.map((e) => e[idKey]));
				}
				value.forEach((e) => {
					e[idKey] && o.set(e[idKey], e);
				});
				this.state.selectedValues = o;
				this._value = new Map(JSON.parse(JSON.stringify(this.state.selectedValues)));
				referVal = '';
			} else {
				!isShow && (this.state.selectedValues = new Map(JSON.parse(JSON.stringify(this._value))));
			}
		} else {
			// 单选
			if (([ ...(this._value || new Map()).keys() ][0] || '') !== ((value || {})[idKey] || '')) {
				if (alwaysCheckValue && value && value[idKey]) {
					value = await this.checkValue([ value[idKey] ]);
				}
				if (value && value[idKey]) {
					this.state.selectedValues = new Map(
						Object.entries({
							[value[idKey]]: value
						})
					);
					this._value = new Map(JSON.parse(JSON.stringify(this.state.selectedValues)));
					refType === 'tree' && (this.state.selectedKeys = [ value[idKey] ]);

					referVal = '';
				} else if (!(value && value[idKey])) {
					referVal = '';
					this.state.selectedValues = new Map();
					this._value = new Map(JSON.parse(JSON.stringify(this.state.selectedValues)));
					refType === 'tree' && (this.state.selectedKeys = []);
				}
			} else {
				this.state.selectedValues = new Map(JSON.parse(JSON.stringify(this._value)));
			}
		}
		this.setState({
			selectedValues: this.state.selectedValues,
			selectedKeys: this.state.selectedKeys,
			treeData,
			columnConfig: typeof this.getColumnConfig === 'function' && this.getColumnConfig(props)
			// referVal
		});
	};
	foolValueToValue = (fool) => {
		const { isMultiSelectedEnabled } = this.props;
		let finalVal;
		fool.display = fool.display || '';
		fool.value = fool.value || '';
		if (isMultiSelectedEnabled) {
			let { display, value, ...arr } = fool;
			//多选 { display: '1,3', value: '2,4' } => [{refname: '1', refpk: '2'}, {refname: '3', refpk: '4'}]
			let foolDisplays = fool.display.split(','),
				foolValues = fool.value.split(',');
			finalVal = [];
			!foolDisplays[0] && (foolDisplays = []);
			!foolValues[0] && (foolValues = []);
			foolDisplays.forEach((e, i) => {
				finalVal.push(
					this.foolToSmart({
						...(arr[i] || {}),
						display: e,
						value: foolValues[i]
					})
				);
			});
			// finalVal = arr;
		} else {
			finalVal = this.foolToSmart(fool);
		}

		return finalVal;
	};
	foolToSmart = (fool) => {
		//单选 { display: '1', value: '2' } => {refname: '1', refpk: '2'}
		const { fieldDisplayed, fieldValued, idKey } = this.props;
		fool.display = fool.display || '';
		fool.value = fool.value || '';
		let foolDisplay = fool.display.split('/'),
			foolValue = fool.value,
			smart = { ...fool };
		Array.isArray(fieldDisplayed)
			? fieldDisplayed.forEach((e, i) => {
					smart[e] = foolDisplay[i];
				})
			: (smart[fieldDisplayed] = foolDisplay[0]);
		smart[fieldValued] = foolValue;
		smart[idKey] = smart[idKey] || foolValue;
		typeof fieldDisplayed === 'string' && (smart[fieldDisplayed] = smart[fieldDisplayed] || smart[fieldValued]);
		return smart;
	};
	//是否在缓存中
	hasCache = (url, param) => {
		// let { cacheData } = this,
		let cacheData = ViewModel.getData('referCache') || [],
			cache = cacheData.find((e) => {
				return e.url === url && compare(param, e.param);
			});
		if (cache) {
			this.setCacheData(url, param, cache.data);
			return cache.data;
		} else {
			return null;
		}
	};
	//将数据存在缓存
	setCacheData = (url, param, data) => {
		param = JSON.parse(JSON.stringify(param));
		data = JSON.parse(JSON.stringify(data));
		let { maxCacheNum } = this.props,
			cacheData = ViewModel.getData('referCache') || [];
		let i = cacheData.findIndex((e) => {
			return e.url === url && compare(param, e.param);
		});
		if (i > -1) {
			cacheData.splice(i, 1);
		}

		cacheData.push({
			url,
			param,
			data
		});
		while (cacheData.length > 10) {
			cacheData.shift();
		}
		ViewModel.setData('referCache', cacheData);
	};
	clearCache = () => {
		ViewModel.setData('referCache', []);
	};
	getParam = () => {
		return {};
	};
	//请求参数
	__getParam = (param = {}) => {
		let {
				queryCondition,
				pageSize,
				refType,
				unitProps,
				isShowUnit,
				isShowDisabledData,
				isHasDisabledData,
				pk_defdoclist,
				dataPowerOperationCode,
				isDataPowerEnable,
				queryGridUrl,
				idKey
			} = this.props,
			{ activeKey } = this.state,
			{ keyword = '', pid = '', pageInfo = {}, searchPks } = param;
		pageInfo = {
			pageSize: pageInfo.pageSize || pageSize,
			pageIndex: pageInfo.pageIndex || (refType === 'tree' ? -1 : 0)
		};
		if (pageInfo.pageSize == -1) {
			pageInfo.pageIndex = -1;
		}

		let extraCondition = {};
		unitProps &&
			isShowUnit &&
			(extraCondition[unitProps.key || 'unitPks'] =
				Array.isArray(this.state.unit) && this.state.unit.length
					? this.state.unit.map((e) => e[idKey]).join(',')
					: (this.state.unit || {})[idKey]);
		isHasDisabledData && isShowDisabledData && (extraCondition.isShowDisabledData = this.state.isShowDisabledData);
		pk_defdoclist !== undefined && (extraCondition.pk_defdoclist = pk_defdoclist);
		dataPowerOperationCode !== undefined && (extraCondition.DataPowerOperationCode = dataPowerOperationCode);
		isDataPowerEnable !== undefined && (extraCondition.isDataPowerEnable = isDataPowerEnable);
		extraCondition.isShowUnit = !!isShowUnit;
		if (activeKey === '1') {
			extraCondition.isShowUsual = true;
			extraCondition.UsualActionURL = queryGridUrl;
		}

		let final = {
			pid, // 对应的树节点
			keyword,
			queryCondition: {
				...(typeof queryCondition === 'function'
					? queryCondition(this.props)
					: typeof queryCondition === 'object' ? queryCondition : {})
			},
			pageInfo,
			...this.getParam(param || {})
		};

		final.queryCondition = { ...extraCondition, ...final.queryCondition };
		if (Array.isArray(searchPks) && searchPks.length) {
			final.searchPks = searchPks;
			final.pageInfo.pageSize = searchPks.length;
		}
		return final;
	};
	//请求列表数据
	loadTableData = async (param) => {
		return await new Promise((resolve) => {
			this.setState(
				{
					loading: true
				},
				() => {
					let { tableData, currentLevel, referVal } = this.state;
					let { queryGridUrl, queryCondition, isCacheable } = this.props;
					ajax({
						url: queryGridUrl,
						data: param,
						loading: false,
						success: (res) => {
							this.setState({
								loading: false
							});
							if (!res.success) {
								throw new Error(res.error.message);
								return;
							}
							isCacheable && this.setCacheData(queryGridUrl, param, res.data);
							resolve(res.data);
						},
						error: (e) => {
							toast({ color: 'danger', content: e.message });
							this.setState({
								loading: false
							});
							throw new Error(e);
						}
					});
				}
			);
		});
	};
	//请求树数据
	loadTreeData = async (param) => {
		return await new Promise((resolve) => {
			this.setState(
				{
					loading: true
				},
				() => {
					let { currentLevel, referVal } = this.state;
					let { queryTreeUrl, queryCondition, isCacheable } = this.props;
					ajax({
						url: queryTreeUrl,
						data: param,
						loading: false,
						success: (res) => {
							this.setState({
								loading: false
							});
							if (!res.success) {
								throw new Error(res.error.message);
								return;
							}
							isCacheable && this.setCacheData(queryTreeUrl, param, res.data);
							resolve(res.data);
						},
						error: (e) => {
							toast({ color: 'danger', content: e.message });
							this.setState({
								loading: false
							});
							throw new Error(e);
						}
					});
				}
			);
		});
	};
	// 设置表格数据
	setTableData = (target, level, data) => {
		let targetData = this.state[target];
		targetData[level] = {
			page: data.page,
			rows: (targetData[level].rows || []).concat(data.rows)
		};
		this.setState({
			[target]: targetData
		});
	};
	// 设置树数据
	setTreeData = (target, parentNode, data, cb) => {
		let { appendToTreeNode, isTreelazyLoad, idKey, pidKey } = this.props,
			{ expandedKeys } = this.state,
			groupTreeData,
			mapOfExpandedKeys;

		// 避免重复
		let map = new Map();
		let treeData = this.state[target];
		treeData = treeData.concat(data.rows);
		treeData.forEach((e) => {
			!map.get(e[idKey]) && map.set(e[idKey], e);
		});
		treeData = [ ...map.values() ];

		if (appendToTreeNode && isTreelazyLoad) {
			let targetNode, path;
			// 记录父节点的路径，保持唯一
			path = parentNode._path || parentNode[idKey];
			data.rows.forEach((e) => {
				// 每一个节点存有一个_path属性，用来表示改节点的所有上级路径
				e._path = path + '=>' + e[idKey];
				e._display = this.props.treeConfig.code
					.map(
						(item) =>
							e[item] ||
							(e.values && e.values[item] && (e.values[item].display || e.values[item].value)) ||
							''
					)
					.join(' ');
				e[pidKey] = e[pidKey] || parentNode[idKey];
				// targetNode.set(e[idKey], new Map(Object.entries(e)));
				// 新加进来的节点，展开都设为false
				let i = expandedKeys.findIndex((ele) => ele === e);
				i !== -1 && expandedKeys.splice(i, 1);
			});
		} else {
			let { isTreeSearch } = this.state,
				{ isTreelazyLoad, rootNode } = this.props,
				deleteKeys = [];

			data.rows.forEach((e) => {
				// 处理树节点显示字段
				e._display = this.props.treeConfig.code
					.map(
						(item) =>
							e[item] ||
							(e.values && e.values[item] && (e.values[item].display || e.values[item].value)) ||
							''
					)
					.join(' ');
				e[pidKey] = e[pidKey] || parentNode[idKey];
			});

			if (isTreeSearch || !isTreelazyLoad) {
				// 非懒加载
				treeData = [ this.props.rootNode ].concat(data.rows);
			}
		}
		({ groupTreeData, mapOfExpandedKeys, treeData } = this.group(treeData, expandedKeys));
		this.setState(
			{
				[target]: treeData,
				expandedKeys,
				groupTreeData,
				mapOfExpandedKeys
			},
			() => {
				typeof cb === 'function' && cb(data);
			}
		);
	};
	group = (treeData, expandedKeys) => {
		const { pidKey } = this.props;
		let groupTreeData = {},
			mapOfExpandedKeys = {};
		treeData.forEach((e) => {
			groupTreeData[e[pidKey]] ? groupTreeData[e[pidKey]].push(e) : (groupTreeData[e[pidKey]] = [ e ]);
		});
		// array => object
		expandedKeys.forEach((e) => {
			mapOfExpandedKeys[e] = true;
		});
		// 构造树结构
		let groupToTree = this.groupToTree(groupTreeData, groupTreeData['undefined']);
		// 转回数组结构，其中包含了disabled属性和level属性
		let treeToArray = this.treeToArray(groupToTree);
		return {
			groupTreeData,
			mapOfExpandedKeys,
			treeData: treeToArray
		};
	};

	groupToTree = (group, data, level = 0) => {
		const { onlyLeafCanSelect, idKey: id, isTreeSearch } = this.props;
		if (typeof data === 'boolean' || data === undefined) {
			if (data === true || data === undefined || isTreeSearch) {
				return '';
			} else if (data === false) {
				return [];
			}
		}
		return data.map((e, i) => {
			let { _display, ...otherProps } = e,
				refpk = e[id],
				disabled = !((onlyLeafCanSelect && e.isleaf) || !onlyLeafCanSelect);
			if (!disabled && e.values && e.values.ReturnOnTree) {
				disabled = e.values.ReturnOnTree.display === 'N' || e.values.ReturnOnTree.value === 'N';
			}
			e.disabled = disabled;
			e.level = level;
			e.children = this.groupToTree(group, group[refpk] || e.isleaf, level + 1);
			return e;
		});
	};

	treeToArray = (tree, arr = []) => {
		if (Array.isArray(tree)) {
			tree.forEach((e) => {
				let { children, ...others } = e;
				arr.push({
					...others
				});
				if (Array.isArray(e.children)) {
					this.treeToArray(e.children, arr);
				}
			});
		}

		return arr;
	};

	getOneDisplayOrValue = (item, DisplayOrValue) => {
		let { fieldDisplayed, fieldValued } = this.props,
			field;
		if (DisplayOrValue === 'display') {
			field = fieldDisplayed;
		} else if (DisplayOrValue === 'value') {
			field = fieldValued;
		} else {
			return '';
		}
		return Array.isArray(field)
			? field
					.map(
						(e) =>
							item[e] ||
							(item.values && item.values[e] && (item.values[e].display || item.values[e].value)) ||
							''
					)
					.join('/')
			: item[field] ||
				(item.values && item.values[field] && (item.values[field].display || item.values[field].value)) ||
				'';
	};
	getDisplayOrValue = (items, DisplayOrValue) => {
		return Array.isArray(items)
			? items.map((e) => this.getOneDisplayOrValue(e, DisplayOrValue)).join(',')
			: this.getOneDisplayOrValue(items, DisplayOrValue);
	};

	// 单选的方法
	singleSelect = (item) => {
		console.log('li2=',item);
		document.body.style.overflow = '';
		// 单选
		let {
				hotDataSize,
				columnConfig,
				onChange,
				extraOnChange,
				refCode,
				isMultiSelectedEnabled,
				onBlur,
				fieldDisplayed,
				fieldValued,
				value: _value,
				foolValue,
				idKey
			} = this.props,
			value = foolValue ? this.foolValueToValue(foolValue) : _value;
		let { selectedValues, isShow, dropDownShow, referVal, currentLevel } = this.state;
		item = item || [ ...selectedValues.values() ][0];
		// 判断前后两次选中的值是否一样，一样的话则不做任何处理
		// let isEqual = (item || {})[idKey] === (value || {})[idKey];
		let isEqual = false;
		// 两次值不一样时，选中并触发onChange事件
		if (item && /^#.*#$/.test(item[idKey])) {
			selectedValues = new Map();
		}
		if (item && item[idKey]) {
			if (isMultiSelectedEnabled) {
				// 存入已选择
				selectedValues.has(item[idKey])
					? selectedValues.delete(item[idKey])
					: selectedValues.set(item[idKey], item);

				this.setState(
					{
						selectedValues
					},
					() => {
						let selections = this.getSelections(),
							foolDisplay = this.getDisplayOrValue(selections, 'display'),
							foolValue = this.getDisplayOrValue(selections, 'value');

						!isEqual &&
							typeof onChange === 'function' &&
							onChange(selections, { display: foolDisplay, value: foolValue });
						!isEqual &&
							typeof extraOnChange === 'function' &&
							extraOnChange(selections, { display: foolDisplay, value: foolValue });
						typeof onBlur === 'function' && onBlur(selections, { display: foolDisplay, value: foolValue });
					}
				);
			} else {
				// 存入已选择
				selectedValues = new Map(
					Object.entries({
						[item[idKey]]: item
					})
				);

				this.setState(
					{
						selectedValues
					},
					() => {
						let foolDisplay = this.getDisplayOrValue(item, 'display'),
							foolValue = this.getDisplayOrValue(item, 'value');

						!isEqual &&
							typeof onChange === 'function' &&
							onChange(item, { display: foolDisplay, value: foolValue });
						!isEqual &&
							typeof extraOnChange === 'function' &&
							extraOnChange(item, { display: foolDisplay, value: foolValue });
						typeof onBlur === 'function' && onBlur(item, { display: foolDisplay, value: foolValue });
					}
				);
			}
			//存入历史记录localStorage
			this.pushToLocal(item);
		} else {
			selectedValues = new Map();
			this.setState(
				{
					selectedValues
				},
				() => {
					!isEqual && typeof onChange === 'function' && onChange({}, { display: '', value: '' });
					!isEqual && typeof extraOnChange === 'function' && extraOnChange({}, { display: '', value: '' });
					typeof onBlur === 'function' && onBlur({}, { display: '', value: '' });
				}
			);
		}
		// 单选或者历史记录时收起，其余不收起
		if (!isMultiSelectedEnabled || (dropDownShow && !referVal)) {
			this.setState({
				isShow: false,
				dropDownShow: false,
				isSelectedShow: false
			});
		}
		this.setState({
			isMoreButtonShow: false
		});
	};
	// 多选的方法
	multiSelect = () => {
		document.body.style.overflow = '';
		let selections = this.getSelections(),
			trueValue = [ ...this._value.values() ],
			{ onChange, extraOnChange, onBlur, fieldDisplayed, fieldValued, idKey } = this.props,
			{ runWithChildren } = this.state;
		if (selections.length > MAX_CHOICE) {
			alert(`最多选择${MAX_CHOICE}条数据`);
			return;
		}
		// let isEqual = compareArr(selections.map((e) => e[idKey]), [ ...this._value.keys() ]);
		let isEqual = false;
		// (trueValue.length &&
		// 	trueValue.some(
		// 		(e) =>
		// 			e.runWithChildren == runWithChildren ||
		// 			(e.runWithChildren === undefined && runWithChildren === false)
		// 	)) ||
		// 	(isEqual = false);
		isEqual || this.pushToLocal([ ...selections ]);

		let index = selections.length > 1 ? selections.findIndex((e) => /^#.*#$/.test(e[idKey])) : -1;
		if (index !== -1) {
			selections.splice(index, 1);
		}

		this.setState(
			{
				referVal: '',
				isShow: false,
				isMoreButtonShow: false
			},
			() => {
				let foolDisplay = this.getDisplayOrValue(selections, 'display'),
					foolValue = this.getDisplayOrValue(selections, 'value');
				!isEqual &&
					typeof onChange === 'function' &&
					onChange(selections, { display: foolDisplay, value: foolValue });
				!isEqual &&
					typeof extraOnChange === 'function' &&
					extraOnChange(selections, { display: foolDisplay, value: foolValue });
				typeof onBlur === 'function' && onBlur(selections, { display: foolDisplay, value: foolValue });
			}
		);
	};
	// 获取已选择的数据
	getSelections = () => {
		const { rootNode, queryTreeUrl, idKey, isRunWithChildren } = this.props;
		let { runWithChildren } = this.state;
		console.log('li =', this.state.selectedValues);
		return [ ...this.state.selectedValues.values() ].filter((e) => e[idKey] !== rootNode[idKey]).map((e) => {
			if (isRunWithChildren && runWithChildren) {
				e.runWithChildren = runWithChildren;
				e.queryTreeUrl = queryTreeUrl;
			} else {
				delete e.runWithChildren;
				delete e.queryTreeUrl;
			}
			return e;
		});
	};
	// 搜索的请求
	dropDownSearch = () => {
		let that = this,
			{ refType, pageSize, queryGridUrl, queryTreeUrl, isCacheable } = this.props,
			{ referVal } = this.state,
			param,
			cacheData;
		param = this.__getParam({
			keyword: referVal
		});
		cacheData = this.hasCache(refType === 'tree' ? queryTreeUrl : queryGridUrl, param);
		if (isCacheable && cacheData) {
			let { dropDownList } = that.state;
			dropDownList.rows = cacheData.rows;
			that.setState({
				dropDownList
			});
		} else {
			(refType === 'tree' ? this.loadTreeData : this.loadTableData)(param).then((data) => {
				let { dropDownList } = that.state;
				dropDownList.rows = data.rows;
				that.setState({
					dropDownList
				});
			});
		}
	};
	// 失去焦点的事件
	blur = (e) => {
		let { selectedValues, referVal, dropDownShow, dropDownList } = this.state,
			{ isMultiSelectedEnabled, checkStrictly, fieldDisplayed, fieldValued, idKey } = this.props;
		// 不检查、有下拉、没有已选值、输入框有值
		if (!checkStrictly && dropDownShow && ![ ...selectedValues.values() ].length && referVal) {
			// if (dropDownList.rows[0]) {
			// 	// 搜索态且搜索结果有值
			// 	isMultiSelectedEnabled
			// 		? (selectedValues.set(dropDownList.rows[0][idKey], dropDownList.rows[0]), this.multiSelect())
			// 		: this.singleSelect(dropDownList.rows[0]);
			// } else {
			let item = {};
			Array.isArray(fieldDisplayed)
				? fieldDisplayed.forEach((e) => {
						item[e] = referVal;
					})
				: (item[fieldDisplayed] = referVal);
			Array.isArray(fieldValued)
				? fieldValued.forEach((e) => {
						item[e] = referVal;
					})
				: (item[fieldValued] = referVal);
			isMultiSelectedEnabled
				? (selectedValues.set(item[idKey], item), this.multiSelect())
				: this.singleSelect(item);
			// }
		} else {
			isMultiSelectedEnabled ? this.multiSelect() : this.singleSelect(this.getSelections()[0]);
		}

		document.body.style.overflow = '';

		this.setState({
			referVal: ''
		});
	};

	pushToLocal = (item, key = 'referHistory') => {
		// 一个或多个
		let isArray = Array.isArray(item),
			queryCondition = this.__getParam().queryCondition,
			{ refCode, columnConfig, refType, hotDataSize, queryGridUrl, queryTreeUrl, idKey } = this.props,
			historyKey = queryGridUrl + queryTreeUrl,
			ReferHistory = JSON.parse(localStorage[key] || '{}'),
			history = ReferHistory[historyKey] || [];
		// 去除重复的
		for (let i = history.length - 1; i >= 0; i--) {
			let flag = isArray ? item.find((e) => history[i][idKey] === e[idKey]) : history[i][idKey] === item[idKey];
			flag && history.splice(i, 1);
		}
		// 添加新的
		isArray ? history.unshift(...item.reverse()) : history.unshift(item);
		if (history.length > hotDataSize) {
			history = history.slice(0, hotDataSize);
		}
		ReferHistory[historyKey] = history;
		localStorage[key] = JSON.stringify(ReferHistory);
	};

	localToList = (key = 'referHistory') => {
		let { refCode, columnConfig, refType, queryGridUrl, queryTreeUrl } = this.props,
			historyKey = queryGridUrl + queryTreeUrl;

		return JSON.parse(localStorage[key] || '{}')[historyKey] || [];
	};
	// 输入框输入事件
	handleInput = (val) => {
		let {
				disabled,
				refCode,
				columnConfig,
				showHistory,
				queryGridUrl,
				queryTreeUrl,
				isCacheable,
				refType,
				hotDataSize,
				idKey
			} = this.props,
			{ dropDownList } = this.state;
		if (disabled) {
			return false;
		}
		if (val) {
			// 有值搜索
			dropDownList.rows = [];
			this.interval = new Date().getTime();
			let s = setTimeout(() => {
				//停止输入0.5s后执行
				if (new Date().getTime() - this.interval >= 500) {
					if (this.state.referVal) {
						this.state.dropDownList.rows = [];
						this.dropDownSearch();
					}
				}
				clearTimeout(s);
			}, 500);
		} else if (showHistory) {
			// 没值历史记录
			let history = this.localToList('referHistory');
			// dropDownList.rows = this.localToList('referHistory');

			if (history.map((e) => e[idKey]).length) {
				let param, cacheData;
				param = this.__getParam({
					pageInfo: { pageSize: hotDataSize },
					searchPks: history.map((e) => e[idKey])
				});
				if (refType === 'tree') {
					cacheData = this.hasCache(queryTreeUrl, param);
				} else {
					cacheData = this.hasCache(queryGridUrl, param);
				}

				if (isCacheable && cacheData) {
					this.setState({
						loading: false,
						dropDownList: cacheData
					});
				} else {
					if (refType === 'tree') {
						this.loadTreeData(param).then((data) => {
							if (!val) {
								this.setState({
									loading: false,
									dropDownList: data
								});
							}
						});
					} else if (queryGridUrl) {
						this.loadTableData(param).then((data) => {
							if (!val) {
								this.setState({
									loading: false,
									dropDownList: data
								});
							}
						});
					}
				}
			} else {
				dropDownList.rows = [];
				this.setState({
					loading: false,
					dropDownList
				});
			}
		}
		this.setState({
			referVal: val,
			dropDownShow: !!(val || showHistory),
			isShow: false,
			dropDownList,
			isSelectedShow: false
		});
	};
	// 点击参照以外的区域收起下拉
	close = (e) => {
		let { isShow, dropDownShow, isSelectedShow } = this.state;
		const { onBlur, type } = this.props,
			{ target } = e;
		if (
			((isShow && type === 'dropDown') || dropDownShow || isSelectedShow) &&
			!(
				(this.popover && this.popover.contains(target)) ||
				(this.referContainer && this.referContainer.contains(target)) ||
				(this.searchList && this.searchList.contains(target))
			)
		) {
			// 失去焦点校验
			this.blur(e);
			this.setState({
				isShow: false,
				dropDownShow: false,
				isSelectedShow: false,
				loading: false
			});
			return;
		}
		if (this.state.isMoreButtonShow) {
			this.setState({
				isMoreButtonShow: false
			});
		}
	};
	// 在参照输入框中删除
	delItem = () => {
		let { selectedValues } = this.state;
		const { isMultiSelectedEnabled, disabled } = this.props;
		if (disabled) {
			return false;
		}
		this.setState(
			{
				selectedValues: new Map(),
				referVal: ''
			},
			() => {
				isMultiSelectedEnabled ? this.multiSelect() : this.singleSelect({});
			}
		);
	};
	// 计算参照输入框位置
	calcFixed = (dom, container = this.props.container) => {
		if (dom && dom !== container) {
			var pos = dom.getBoundingClientRect(),
				top = pos.top,
				left = pos.left,
				referHeight = this.referContainer.offsetHeight,
				dropDownWidth = this.referContainer.offsetWidth * this.props.columnConfig.length;
			if (top + referHeight + 204 > document.body.clientHeight) {
				top = top - referHeight - 208;
			}
			if (left + dropDownWidth > document.body.clientWidth) {
				left = pos.right - dropDownWidth;
			}
			return {
				top,
				left
			};
		} else {
			return {
				top: 0,
				left: 0
			};
		}
	};
	// 选择函数时的点击事件
	onFunctionClick = (item) => {
		const { isMultiSelectedEnabled, fieldDisplayed, fieldValued } = this.props;
		let value = {
			refname: item.name,
			refpk: item.code
		};
		Array.isArray(fieldDisplayed)
			? fieldDisplayed.forEach((e) => {
					value[e] = item.name;
				})
			: (value[fieldDisplayed] = item.name);
		Array.isArray(fieldValued)
			? fieldValued.forEach((e) => {
					value[e] = item.code;
				})
			: (value[fieldValued] = item.code);
		if (isMultiSelectedEnabled) {
			this.setState(
				{
					selectedValues: new Map([ [ item.code, value ] ])
				},
				function() {
					this.multiSelect();
				}
			);
		} else {
			this.singleSelect(value);
		}
	};

	// 点击参照三个点的事件
	show = (e) => {
		throw new Error('show方法未复写！');
	};

	renderDropDown = () => {
		return null;
	};

	renderPopover = () => {
		return null;
	};

	renderPopoverHeader = () => {
		return null;
	};

	renderPopoverHeaderExtend = () => {
		return null;
	};

	renderPopoverSearchArea = () => {
		return null;
	};

	renderPopoverSearchLeftArea = () => {
		return null;
	};

	renderPopoverSearchExtendArea = () => {
		return null;
	};

	renderPopoverContain = () => {
		return null;
	};

	renderPopoverLeft = () => {
		return null;
	};
	renderPopoverLeftHeader = () => {
		return null;
	};
	renderPopoverRight = () => {
		return null;
	};
	renderPopoverPageArea = () => {
		return null;
	};
	renderPopoverBottom = () => {
		return null;
	};
	renderPopoverBottomExtend = () => {
		return null;
	};

	getLabel = (item) => {
		const { fieldDisplayed } = this.props;
		return this.getDisplayOrValue(item, 'display');
	};

	beforeEvent = (onFocus, callback, event) => {
		const { isMultiSelectedEnabled } = this.props;
		if (typeof onFocus === 'function') {
			let res = onFocus(isMultiSelectedEnabled ? this.getSelections() : this.getSelections()[0] || {}, event);
			if (res instanceof Promise) {
				res.then((isContinue) => {
					// 容错undefined
					if (isContinue !== false) {
						typeof callback === 'function' && callback();
					}
				});
			} else if (res !== false) {
				// 容错undefined
				typeof callback === 'function' && callback();
			}
		} else {
			typeof callback === 'function' && callback();
		}
	};

	render() {
		let {
			tableData,
			isShow,
			currentLevel,
			referVal,
			loading,
			dropDownList,
			dropDownShow,
			selectedValues,
			isSelectedShow
		} = this.state;
		let { fixTop, fixLeft } = this;
		let length = Object.keys(tableData).length;
		let {
			value,
			refName,
			refCode,
			disabled,
			refModelUrl,
			isFixed,
			columnConfig,
			showHistory,
			ellipsis,
			referClassName,
			className,
			style,
			placeholder,
			container,
			isMultiSelectedEnabled,
			autoFocus,
			onBlur,
			onFocus,
			fieldDisplayed,
			fieldValued,
			functions,
			idKey,
			showTitle,
			onlyLeafCanSelect,
			refType
		} = this.props;
		// let trueValue = isShow ? [ ...(this._value || new Map()).values() ] : this.getSelections(),
		let trueValue = [ ...(this._value || new Map()).values() ],
			firstValue = trueValue[0],
			firstLabel;
		if (firstValue) {
			firstLabel = this.getLabel(firstValue);
		}
		let timeout;
		let allLabel = trueValue.map((e) => this.getLabel(e)).join(',');
		this.times = this.times || 0;
		return (
			<div className={'refer-wrapper clearfix'} style={{ display: 'flex' }}>
				<Dropdown
					trigger={[ 'click' ]}
					overlayClassName={'refer-container'}
					placement={'bottomLeft'}
					ref={(dom) => {
						this.referContainer = ReactDOM.findDOMNode(dom);
					}}
					animation="slide-up"
					visible={dropDownShow || isSelectedShow}
					overlay={
						<DropDownOfRefer
							ref={(dom) => {
								this.searchList = ReactDOM.findDOMNode(dom);
							}}
							dropDownShow={dropDownShow}
							referVal={referVal}
							isSelectedShow={isSelectedShow}
							columnConfig={columnConfig}
							dropDownList={dropDownList}
							selectedValues={selectedValues}
							getSelections={this.getSelections}
							singleSelect={this.singleSelect}
							hiddenInput={this.hiddenInput}
							maxWidth={
								(dropDownShow || isSelectedShow) &&
								this.referContainer &&
								this.referContainer.offsetWidth
							}
							loading={loading}
							idKey={idKey}
							onlyLeafCanSelect={onlyLeafCanSelect}
							refType={refType}
						/>
					}
					getPopupContainer={() => {
						return container;
					}}
				>
					{this.clickContainer || (
						<Col
							xs={12}
							className={`refer clearfix ${referClassName}`}
							style={{
								backgroundColor: disabled ? '#F3F3F3' : '#fff',
								...style
							}}
						>
							<div title={showTitle ? allLabel : ''}>
								<ul>
									{firstLabel && (
										<li
											className={`selected-item refer-class ${isMultiSelectedEnabled
												? ''
												: 'single-select'}`}
											onClick={(e) => {
												e.stopPropagation();
												if (!disabled) {
													this.beforeEvent(
														onFocus,
														() => {
															this.setState({
																isSelectedShow: true,
																dropDownShow: false
															});
														},
														event
													);
												}
											}}
										>
											<span>{firstLabel}</span>
										</li>
									)}
									{trueValue.length > 1 ? (
										<li
											className="selected-num refer-class"
											onClick={(e) => {
												e.stopPropagation();
												if (!disabled) {
													this.beforeEvent(
														onFocus,
														() => {
															this.setState({
																isSelectedShow: true,
																dropDownShow: false
															});
														},
														event
													);
												}
											}}
										>
											+{trueValue.length - 1}
										</li>
									) : null}
									<li
										className={`selected-input ${trueValue.length > 0
											? 'selected-input-hidden'
											: ''}`}
									>
										<input
											type="text"
											ref={(dom) => {
												this.hiddenInput = dom;
											}}
											value={allLabel}
											className="hidden-input"
										/>
										{trueValue.length === 0 && (
											<FormControl
												ref={(dom) => {
													this.referInput = ReactDOM.findDOMNode(dom);
												}}
												className={`refer-input ${className}`}
												disabled={!!disabled}
												value={referVal || ''}
												onChange={this.handleInput}
												onFocus={(event) => {
													this.beforeEvent(
														onFocus,
														this.handleInput.bind(this, referVal),
														event
													);
												}}
												placeholder={
													(this.state.isShow
														? [ ...(this._value || new Map()).values() ]
														: this.getSelections()).length ? (
														''
													) : (
														placeholder
													)
												}
												autoFocus={autoFocus}
											/>
										)}
									</li>
								</ul>
							</div>
							{!disabled &&
							trueValue.length > 0 && (
								<i
									className="refer-del-i iconfont icon-qingkong"
									onClick={(event) => {
										this.beforeEvent(onFocus, this.delItem, event);
									}}
								/>
							)}
							<span
								className="icon-refer"
								style={{ cursor: disabled ? 'not-allowed' : 'pointer' }}
								onClick={(event) => {
									this.beforeEvent(onFocus, this.show.bind(this, event), event);
								}}
							/>
						</Col>
					)}
				</Dropdown>
				{Array.isArray(functions) && functions.length ? disabled ? (
					<span
						class="refer-function disabled"
						style={{
							backgroundColor: disabled ? '#eee' : '#fff'
						}}
					>
						fx
					</span>
				) : (
					<Dropdown
						trigger={[ 'click' ]}
						overlayClassName={'refer-function-dropdown'}
						placement={'bottomLeft'}
						// visible={true}
						overlay={
							<ul>
								{functions.map((e) => <li onClick={this.onFunctionClick.bind(this, e)}>{e.name}</li>)}
							</ul>
						}
					>
						<span class="refer-function">fx</span>
					</Dropdown>
				) : null}
				{this.renderDropDown()}
				{this.renderPopover()}
			</div>
		);
	}
}

BaseRefer.propTypes = {
	value: PropTypes.oneOfType([ PropTypes.object, PropTypes.array ]), //参照的值
	foolValue: PropTypes.object, //{display, value}的值
	onChange: PropTypes.func, //onChange事件
	extraOnChange: PropTypes.func, //额外的onChange事件
	refName: PropTypes.string, //参照名称
	refCode: PropTypes.string, //参照编码
	refType: PropTypes.oneOf([ 'tree', 'grid', 'gridTree' ]), //参照类型
	type: PropTypes.oneOf([ 'dropDown', 'popover' ]), //参照展示类型
	columnConfig: PropTypes.array, //列信息配置
	treeConfig: PropTypes.object, //树信息配置
	hotDataSize: PropTypes.number, //历史记录条数
	pageSize: PropTypes.number, //分页请求数据时每页条数
	disabled: PropTypes.bool, //是否禁用
	referClassName: PropTypes.string, //参照最外层classname
	className: PropTypes.string, //参照input的classname
	style: PropTypes.object, //参照最外层样式
	placeholder: PropTypes.string, //占位文字
	showHistory: PropTypes.bool, //是否显示历史记录
	queryGridUrl: PropTypes.string, //查询表格数据的url
	queryTreeUrl: PropTypes.string, //查询树数据的url
	clickContainer: PropTypes.object, // 可以触发参照弹窗事件的容器
	queryCondition: PropTypes.func, //自定义查询条件，会传到后台
	isCacheable: PropTypes.bool, // 是否启用数据缓存
	container: PropTypes.object, //容器
	isMultiSelectedEnabled: PropTypes.bool, // 是否多选
	onTreeNodeExpand: PropTypes.func, // 树节点展开事件
	maxCacheNum: PropTypes.number, // 缓存条数
	rootNode: PropTypes.object, // 树的根节点
	autoFocus: PropTypes.bool, // 自动获取焦点
	onBlur: PropTypes.oneOfType([ PropTypes.func, PropTypes.object ]), // 失去焦点事件
	onFocus: PropTypes.oneOfType([ PropTypes.func, PropTypes.object ]), // 获取焦点事件
	onlyLeafCanSelect: PropTypes.bool, // 是否只有树节点可选
	isTreelazyLoad: PropTypes.bool, // 树是否懒加载
	allowSearchConfig: PropTypes.bool, // 是否启用搜索设置
	allowColumnConfig: PropTypes.bool, // 是否启用列设置
	fieldDisplayed: PropTypes.string, // 作为display的字段
	fieldValued: PropTypes.string, // 作为value的字段
	appendToTreeNode: PropTypes.bool, // 懒加载树是否将数据直接插入节点（适用于refpk重复时）
	isShowDisabledData: PropTypes.bool, // 显示停用
	isHasDisabledData: PropTypes.bool, // 是否有【显示停用】功能
	unitProps: PropTypes.object, // 业务单元参照的属性
	isShowUnit: PropTypes.bool, // 业务单元参照的属性
	checkStrictly: PropTypes.bool, // 检验值的准确性（必须是后台有的值）
	popWindowClassName: PropTypes.string, // 添加到弹出层的class
	idKey: PropTypes.string, // 树id对应的key值
	pidKey: PropTypes.string, // 树pid对应的key值
	foolValue: PropTypes.object, // {display, value}
	isShowUsual: PropTypes.bool, // 是否显示常用
	defaultUnitValue: PropTypes.object, // 业务单元默认值
	isShowTableSearch: PropTypes.bool, // 显示表格区的搜索框
	isShowTreeSearch: PropTypes.bool, // 显示树区的搜索框
	functions: PropTypes.array, // 参照函数
	alwaysCheckValue: PropTypes.bool, // 是否每次去后台校验数据
	showTitle: PropTypes.bool, // 鼠标悬停是否显示title
	isRunWithChildren: PropTypes.bool // 是否启用【执行时包含下级】功能
};

